.mypost {
  margin: 20px auto;
  width: 60%;
  height: 40vh;
  background-color: azure;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.mypost_image_container {
  width: 30%;
}
.mypost_image_container img {
  width: 100%;
  height: 37vh;
}

.mypost_text_container {
  width: 60%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mypost_text_container_heading {
  flex: 1;
  padding: 5px 20px 5px 30px;
}

.mypost_text_container_post_content {
  padding: 5px 20px 5px 30px;
  flex: 4;
  overflow: hidden;
}
.mypost_text_container_footer {
  flex: 1;
  display: flex;
  justify-content: space-between;
  padding: 5px 20px 5px 30px;
}
