.home {
  display: flex;
  flex-direction: column;
}
.home_main {
  display: flex;
  justify-content: space-between;
}

.home_content {
  width: 70vw;
  height: 85vh;
}
.home_content_border {
  width: 70%;
  font-size: 1.6vw;
  text-align: center;
  margin: 5vh auto;
  height: 60vh;
  border-right: 1px solid rgb(221, 219, 219);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home_content_border div {
  width: 70%;
  margin: 10px auto;
  padding: 0.5vw;
}

.home_content_border div:nth-child(3) {
  border: 1px solid rgb(248, 212, 212);
}

.home_main_sidebar {
  width: 20%;
  height: 85vh;
}

.home_footer {
  text-align: center;
  background-color: rgb(163, 167, 172);
  border-top: 1px solid rgb(207, 206, 206);
  width: 100%;
  height: 20vh;
}
