.register_component {
  width: 100vw;
  height: 100vh;
  background-image: url("./Icons_and_Images/LP1.jpg");
  background-size: cover;
  position: relative;
}
.register {
  position: absolute;
  top: 15%;
  left: 30%;
  width: 40%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}

.register_form {
  width: 100%;
  height: 80%;
}

.register_container {
  padding: 2px;
}

.register_container input {
  height: 35px;
  width: 80%;
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
}
.already_registered {
  height: 15%;
  background-color: aliceblue;
}
.already_registered div {
  padding-top: 2%;
}
