.LpImage {
  width: 100vw;
}
.landing_container {
  position: relative;
}
.landing_login_register {
  position: absolute;
  top: 5%;
  right: 10%;
}
.landing_login_register a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 20px;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.19);
}
.landing_logo {
  width: 15vw;
  position: absolute;
  top: 1%;
  left: 20%;
}
.landing_logo img {
  width: 100%;
}

.landing_welcome {
  font-size: 1.6vw;
  position: absolute;
  top: 15%;
  left: 35%;
}

.landing_message {
  font-size: 1.3vw;
  position: absolute;
  width: 50%;
  background-color: azure;
  top: 25%;
  left: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 10px;
}
.landing_message p {
  border-top: 1px solid #eee;
  padding: 20px;
  width: 90%;
}
