.navbar {
  margin: 0px;
  height: 11vh;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.12);
  background: aliceblue;
  font-size: 20px;
}
a {
  text-decoration: none;
}
.navbar_ul_left {
  display: flex;
}

.navbar_ul_right {
  display: flex;
}
.navbar_username {
  text-align: center;
  width: 10vw;
  padding: 30px 0;
}
.navbar_logout {
  text-align: center;
  width: 10vw;
  padding: 30px 0;
}
.navbar_home {
  text-align: center;
  padding: 30px 0;
  min-width: 5vw;
  flex-grow: 1;
}
.navbar_create_post {
  text-align: center;
  min-width: 8vw;
  padding: 30px 0;
  flex-grow: 1;
}

.navbar_posts {
  text-align: center;
  width: 15vw;
  flex-grow: 2;
  padding: 30px 0;
  position: relative;
}

.navbar_mypost {
  padding: 10px;
  background-color: gray;
  color: #eee;
  border-bottom: 1px solid #eee;
  border-radius: 5px;
}
.navbar_allpost {
  padding: 10px;
  background-color: gray;
  color: #eee;
  border-bottom: 1px solid #eee;
  border-radius: 5px;
}
.navbar_importantpost {
  padding: 10px;
  background-color: gray;
  color: #eee;
  border-bottom: 1px solid #eee;
  border-radius: 5px;
}
