.login {
  width: 100vw;
  height: 100vh;
  background-image: url("./Icons_and_Images/LP1.jpg");
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login h2 {
  margin: 100px;
}
.login_form {
  width: 50%;
}
.login_form input {
  margin: 5px auto;
  display: block;
  height: 30px;
  width: 60%;
}
.Not_registered {
  margin-top: 5%;
  height: 5%;
  background-color: aliceblue;
}
.Not_registered div {
  padding-top: 2%;
}
